@import url('https://fonts.googleapis.com/css2?family=Pangolin&display=swap');

.Hangman {
  width: 400px;
  color: #e63946;
  margin: 1px auto;
  font-family: 'Pangolin', cursive;
  height: 100%;
}

.Hangman img {
  border: 10px solid white;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
}

.Hangman h1 {
  font-size: 4rem;
  font-weight: 300;
  margin-bottom: 0.1em;
  margin-top: 0;
}

.Hangman-word {
  letter-spacing: 1em;
  margin: 0.4em -1em 0.2em 0;
  font-size: 2rem;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 250px;
  margin-top: 0;
}

.Hangman button {
  border: none;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  margin: 5px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: #bfd200;
  color: #e63946;
  box-shadow: 0 6px #ff9800;
  border-radius: 5px;
  padding-top: 4px;
}

.Hangman button:hover {
  background-color: #ff9800;
}

.Hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  box-shadow: 0 6px #9e9e9e;
}

#reset {
  width: auto;
  display: block;
  margin: 0px auto;
  color: #bfd200;
  background: #e63946;
  box-shadow: 0 6px #775144;
}

#reset:hover {
  background-color: #775144;
}

button:active {
  transform: translateY(2px);
}
